body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.login-page{
    /* align vertical and horizontal */
    padding-top: auto;
    background-color: #f5f5f5;
    margin: 10vh 10vw;
    height: 80vh;
    width: 80vw;
    display: flex;
    justify-content: center;
    align-items: center;
}
.btnGroup{
/* evenly spaced */
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    width: 100%;
    height: 100%;
}
.error-message{
    color: red;
    height: auto;
    width: auto;
    font-size: 1em;
    text-align: center;
    font-style: italic; 
    background-color: rgb(255, 223, 218);
    border-radius: 40px;
}
.backToLoginPageBtn{
    /* put in bottom left corner of .login-page*/
    position: absolute;
    bottom: 10vh;
    left: 10vw;
    margin: 1em;
    border: none;
    background-color: none;


}


